.btn {
  display: flex;
  align-items: center;
  /* border: 3px solid #55c5f3; */
  background-color: #51a7b0;
  color: #ecf1a4;
  font-size: 21px;
  font-weight: 400;
  border: none;
  border-radius: 20px;
  padding: 12px 30px;
}

.btn:hover {
  border: none;
  background-color: #326a70;
  cursor: pointer;
}

.btn-white {
  display: flex;
  align-items: center;
  background: linear-gradient(#fff, #ccc);
  color: #000;
  font-size: 14px;
  border: none;
  border-radius: 4px;
  height: 40px;
  padding: 0px 16px;
  transition: all 200ms;
}

.btn-white:hover {
  background: linear-gradient(#ccc, #fff);
  cursor: pointer;
}

.box-btns {
  display: flex;
  justify-content: space-around;
  margin-top: 35px;
}

.btn-icon {
  position: absolute;
  right: 90px;
  margin-top: -40px;
  margin-bottom: 50px;
  cursor: pointer;
  background-color: transparent;
  border: none;
}

.icon {
  font-size: 24px;
  color: #bbb;
}

.icon:hover {
  color: green;
}

.btn-play {
  cursor: pointer;
  width: 100px;
  height: 100px;
  background-color: #51a7b0 !important;
  border-radius: 100%;
  border: 3px solid #55c5f3;
  font-size: 30px;
  color: #ecf1a4;
  text-transform: uppercase;
  font-family: "Chewy";
}
