.jumbo-movil {
  width: 100%;
  min-height: 230px;
  margin: 20px auto;
}

.jumbo-movil .form-group {
  width: 100%;
}

.jumbo-movil .box-form {
  display: flex;
  width: 90%;
  justify-content: space-around;
  /* flex-direction: column; */
}

.jumbo-movil .d-cont {
  width: 34%;
}

.jumbo-movil .form-group input[type="text"],
.jumbo-movil .form-group input[type="date"],
.jumbo-movil .form-group input[type="time"],
.jumbo-movil .form-group input[type="tel"],
.jumbo-movil .form-group input[type="email"],
.jumbo-movil .form-group select,
.jumbo-movil .form-group textarea {
  border: 3px solid #713f27;
  text-align: center;
}

.header-mobil {
  width: 100%;
  /* height: 70px; */
  /* box-shadow: 0px 0px 5px #ddd; */
  margin-top: 45px;
  margin-bottom: 35px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.header-mobil h1 {
  font-family: "Chewy";
  font-size: 60px;
  color: #ecf1a4;
  -webkit-text-stroke-width: 4px;
  -webkit-text-stroke-color: #73792c;
}

.header-mobil img {
  width: 300px;
}

.info-mobil {
  display: block;
  margin: 0px auto;
  margin-top: 20px;
  margin-bottom: 40px;
  width: 80%;
}

.info-mobil p {
  font-size: 25px;
  text-align: center;
  color: #3f5307;
  font-family: "Glacial Indifference";
}

.ruleta-mobil {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.slot-mobil {
  width: 100px;
  height: 140px !important;
  margin: 10px;
  margin-bottom: 45px;
  overflow: hidden;
  border: 4px solid #713f27;
  border-radius: 18px;
}

.img-slot-mobil {
  width: 100%;
  display: block;
  width: 100px;
  height: 140px;
}

.footer-mobil {
  width: 100%;
  height: 20px;
  margin-top: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #713f27;
  padding-top: 7px;
  padding-bottom: 12px;
}

.footer-mobil a {
  text-decoration: none;
  color: #462719;
  transition: 200ms;
}

.footer-mobil a:hover {
  color: #444;
}
