* {
  margin: 0px;
  padding: 0px;
}

@font-face {
  font-family: "Glacial Indifference";
  src: url("./assets/fonts/glacial-indifference/GlacialIndifference-Regular.otf");
}

@font-face {
  font-family: "Chewy";
  src: url("./assets/fonts/Chewy/Chewy-Regular.ttf");
}

body {
  background-image: url("./assets/images/background.jpg");
  background-repeat: no-repeat, repeat;
  background-size: cover;
}

.header {
  width: 100%;
  /* height: 70px; */
  /* box-shadow: 0px 0px 5px #ddd; */
  margin-top: 45px;
  margin-bottom: 35px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.header h1 {
  font-family: "Chewy";
  font-size: 90px;
  color: #ecf1a4;
  -webkit-text-stroke-width: 4px;
  -webkit-text-stroke-color: #73792c;
}

.header img {
  width: 380px;
}

.principal {
  width: 100%;
  display: flex;
  justify-content: space-between;
  flex-direction: column;
}

.footer {
  width: 100%;
  height: 20px;
  /* background-color: #444; */

  display: flex;
  justify-content: center;
  align-items: center;
  color: #713f27;
  padding-top: 7px;
  padding-bottom: 12px;
}

.footer a {
  text-decoration: none;
  color: #462719;
  transition: 200ms;
}

.footer a:hover {
  color: #444;
}

.jumbo-admin,
.ruleta,
.jumbo {
  width: 45%;
  min-height: 230px;
  margin: 20px auto;
}

.jumbo h1 {
  font-size: 32px;
  color: #444;
  text-align: center;
  margin-bottom: 35px;
}

.jumbo p {
  display: block;
  margin-top: -10px;
  margin-bottom: 30px;
  font-size: 12px;
}

.jumbo .mascara {
  width: 100%;
  height: 150px;
  overflow: hidden;
}

.jumbo-admin {
  width: 95%;
  height: 48vh;
  margin-top: 5px;
  margin-bottom: 50px;
  max-height: 48vh;
  overflow-x: hidden;
  overflow-y: scroll;
  background-color: white;
}

.ruleta {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

/* ESTILOS DE FORMULARIOS  */

input[type="text"],
input[type="date"],
input[type="time"],
input[type="tel"],
input[type="email"],
select,
textarea {
  display: block;
  margin: 0px auto;
  margin-top: 15px;
  padding: 14px 20px;

  border-radius: 18px;
  background: #f6f7f9;
  color: #454545;
  width: 80%;
  opacity: 4;
  outline: 0;
  border-color: transparent;
}

.form-group {
  width: 90%;
  margin: 0px auto;
}

.form-group label {
  display: block;
  text-align: left;
  color: #444;
  margin-bottom: 5px;
  font-size: 14px;
}

.form-group input[type="text"],
.form-group input[type="date"],
.form-group input[type="time"],
.form-group input[type="tel"],
.form-group input[type="email"],
.form-group select,
.form-group textarea {
  border: 3px solid #fcb92e;
  outline: 0;
  width: 100%;
  margin-top: 0px;
  margin-bottom: 15px;
}

.form-group input[type="text"]::placeholder,
.form-group input[type="date"]::placeholder,
.form-group input[type="time"]::placeholder,
.form-group input[type="tel"]::placeholder,
.form-group input[type="email"]::placeholder,
.form-group select::placeholder,
.form-group textarea::placeholder {
  color: #858c1c;
  font-style: italic;
}

.form-group input[type="submit"] {
  margin: 0px auto;
  margin-top: 40px;
}

.form-group span {
  display: block;
  text-align: left;
  color: #444;
  font-size: 12px;
  margin-top: -9px;
  margin-bottom: 25px;
}

.box-form {
  display: flex;
  width: 100%;
  justify-content: space-between;
}

.d-cont {
  width: 45%;
}

.box-map {
  border: 1px solid red;
  min-height: 150px;
}

.codigo {
  display: block;
  width: 80%;
  font-size: 10px;
  text-align: center;
}

.table {
  width: 100%;
  margin: 0px auto;
}

.table th {
  background-color: #ccc;
  font-size: 12px;
  padding: 5px 7px;
  margin-bottom: 5px;
  text-align: center;
  /* border: 2px solid red; */
}

.table td {
  font-size: 12px;
  padding: 5px 7px;
  text-align: center;
}

.slot {
  width: 220px;
  height: 250px !important;
  margin: 20px;
  margin-bottom: 45px;
  /* overflow: hidden; */
  border: 4px solid #713f27;
  border-radius: 18px;
}

.slot-sep {
  padding: 20px;
}

/* .slot-value {
  border: 3px solid orange;
  margin-top: 75px;
} */

.img-slot {
  box-sizing: border-box;
  display: block;
  width: 220px;
  height: 250px;
  /* padding: 5px; */
}

.ruleta button {
  margin-bottom: 50px;
}

.info {
  display: block;
  margin: 10px auto;
  width: 40%;
}

.info p {
  font-size: 25px;
  text-align: center;
  color: #3f5307;
  font-family: "Glacial Indifference";
}
